
export const START_TIMER = "START_TIMER"

export const PAUSE_TIMER = "PAUSE_TIMER"

export const RESET_TIMER = "RESET_TIMER"

// 시간설정

export const SET_MINUTE = "SET_MINUTE"

export function setInputMinnute(input){
  return {
    type: SET_MINUTE,
    minnute: input
  }
}

export function startTimerActionCreator(timerReference){
  return {
    type: START_TIMER,
    timerReference: timerReference
  }
}

// pause timer
export function pauseTimerActionCreator(timerReference){
  return {
    type: PAUSE_TIMER,
    timerReference: timerReference
  }
}

// reset timer
export function resetTimerActionCreator(timerReference){
  return {
    type: RESET_TIMER,
    timerReference:timerReference
  }
}
