import {
  PAUSE_TIMER,
  RESET_TIMER, SET_MINUTE,
  START_TIMER
} from "../../actions/pomodoroAction";

export const pomodoroInitialState = {
  pomodoro: {
    result: {
      interval: 0
    },
    config: {
      minute: 0,
      rest: 0,
      countValue: 0
    },
    timer: {
      state: {
        isRest: true,
        isRunning: false,
        isPause: false,
      }
    }
  }
}

export default function pomodoroReducer(state = pomodoroInitialState, action) {
  if (action.type === PAUSE_TIMER && action.timerReference.current) {
    console.log("pause 버튼 작동")
    return {
      ...state,
      timer: {
        ...state.pomodoro.timer.state,
        isRunning: false,
        isPause: true
      }
    }
    //   RESET_TIMER
  } else if (action.type === RESET_TIMER) {
    return {
      ...state,
      timer: {
        ...state.pomodoro.timer.state,
        isRunning: false,
        isPause: false
      },
      config: {
        ...state.pomodoro.config,
        countValue: state.pomodoro.config.minute * 60
      }
    }
  } else if (action.type === START_TIMER) {
    // --------------------------------------------------
    let pauseState, isRestState
    alert("start버튼 확인")
    if (state.pomodoro.timer.state.isPause) {
      pauseState = true
      isRestState = state.pomodoro.timer.state.isRest
    } else {
      pauseState = false
      isRestState = !state.pomodoro.timer.state.isRest
    }
    return {
      ...state,
      timer: {
        ...state.pomodoro.timer.state,
        isRest: isRestState,
        isRunning: !state.pomodoro.timer.state.isRunning,
        isPause: pauseState
      }
    }
  //   타이머가 다 돌았을때
  } else if (state.pomodoro.config.countValue === 0) {
    let count, interval
    if (state.pomodoro.timer.state.isRest) {
      interval = state.pomodoro.result.interval
      count = state.pomodoro.config.minute * 60
    } else {
      interval = state.pomodoro.result.interval + 1
      count = state.pomodoro.config.rest * 60
    }
    return {
      ...state,
      timer: {
        ...state.pomodoro.timer.state,
        isRunning: !state.pomodoro.timer.state.isRunning,
      },
        config : {
          ...state.pomodoro.config,
          countValue: count
        },
        result: {
          ...state.pomodoro.result,
          interval: interval
        }
    };
  }else if (action.type === SET_MINUTE){
    return {
      ...state,
      config: {
        ...state.pomodoro.config,
        minute: action.minute
      }
    }
  }
  // isWorking true
  return {
    ...state,
    config: {
      ...state.pomodoro.config,
      countValue: state.pomodoro.config.countValue -1
    }
  }
  return state
}
